:root {
    --primary: #fff
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #000;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: var(--primary);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
    align-items: center;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 64px;
    font-size: 24px;
}

.btn--medium:hover, .btn--large:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #000;
    transition: 250ms;
}